import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
import InvoicePdfDefinitionBuilder from './invoice';

pdfMake.vfs = pdfFonts.pdfMake.vfs;

var PdfMaker = function() {};

PdfMaker.prototype.makeInvoicePdf = function(invoice) {
	var dd = new InvoicePdfDefinitionBuilder().build(invoice);
	pdfMake.createPdf(dd).download('Faktúra_' + invoice.id + '.pdf');
};

export { PdfMaker };
