import { textToBase64Barcode } from './utils/barcode';
import { minTableLayout } from './utils/table';

var filterNull = function(item) {
	return item;
};

var DefinitionBuilder = {
	_getItemTable: function(invoice) {
		var body;
		if (invoice.type === 2) {
			body = [
				[
					{ text: 'Označenie dodávky', bold: true },
					{ text: 'Počet m. j.', bold: true, alignment: 'right' },
					{ text: 'Cena za m.j.', bold: true, alignment: 'right' },
					{ text: 'DPH %', bold: true, alignment: 'right' },
					{ text: 'bez DPH', bold: true, alignment: 'right' },
					{ text: 'DPH', bold: true, alignment: 'right' },
					{ text: 's DPH', bold: true, alignment: 'right' }
				]
			];

			invoice.items.forEach(function(item) {
				body.push([
					{ text: item.name },
					{ text: item.amount, alignment: 'right' },
					{ text: item.pricePerUnit, alignment: 'right' },
					{ text: item.taxRate, alignment: 'right' },
					{
						text: item.netPrice,
						alignment: 'right'
					},
					{
						text: item.taxPrice,
						alignment: 'right'
					},
					{
						text: item.grossPrice,
						alignment: 'right'
					}
				]);
			});

			return {
				layout: 'lightHorizontalLines',
				margin: [0, 10, 0, 0],
				//   alignment: 'right',
				fontSize: 9,
				table: {
					// headers are automatically repeated if the table spans over multiple pages
					// you can declare how many rows should be treated as headers
					headerRows: 1,
					widths: ['*', 45, 60, 30, 60, 60, 60],
					body: body
				}
			};
		} else {
			body = [
				[
					{ text: 'Označenie dodávky', bold: true },
					{ text: 'Počet m. j.', bold: true, alignment: 'right' },
					{ text: 'Cena za m.j.', bold: true, alignment: 'right' },
					{ text: 'Spolu', bold: true, alignment: 'right' }
				]
			];

			invoice.items.forEach(function(item) {
				body.push([
					{ text: item.name },
					{ text: item.amount, alignment: 'right' },
					{
						text: item.pricePerUnit,
						alignment: 'right'
					},
					{
						text: item.grossPrice,
						alignment: 'right'
					}
				]);
			});

			return {
				layout: 'lightHorizontalLines',
				margin: [0, 10, 0, 0],
				//   alignment: 'right',
				fontSize: 9,
				table: {
					// headers are automatically repeated if the table spans over multiple pages
					// you can declare how many rows should be treated as headers
					headerRows: 1,
					widths: ['*', 50, 60, 80],
					body: body
				}
			};
		}
	},

	_getPriceTableBody: function(invoice) {
		var body = [
			[
				{ text: 'Spolu pred zľavou:', fontSize: 14, bold: true },
				{ text: invoice.grossPrice, fontSize: 14, bold: true }
			],
			[
				{
					text: 'Zľava' + '   ' + invoice.discount + ' %:',
					fontSize: 13
				},
				{
					text: invoice.discountPrice,
					fontSize: 13
				}
			],
			[
				{ text: 'Spolu na úhradu:', fontSize: 15, bold: true },
				{ text: invoice.netPrice, fontSize: 15, bold: true }
			]
		];

		if (!invoice.discount) {
			body.splice(0, 2);
		}

		return body;
	},

	build: function(invoice) {
		var emitent = invoice.emitent || {},
			recipient = invoice.recipient || {};

		var dd = {
			pageSize: 'A4',
			pageOrientation: 'portrait',
			// [left, top, right, bottom] or [horizontal, vertical] or just a number for equal margins
			pageMargins: [50, 20, 25, 30],
			defaultStyle: {
				font: 'Roboto'
			},
			content: [
				{
					columns: [
						{
							stack: [
								{
									text: emitent.entity,
									fontSize: 11,
									bold: true,
									margin: [0, 0, 0, 5]
								}
							],
							width: '*',
							margin: [0, 0, 0, 0]
						},
						{
							image: textToBase64Barcode(invoice.id),
							width: 100,
							margin: [0, 0, 0, 9]
						}
					],
					fontSize: 8
				},
				{
					columns: [
						{
							stack: [
								{
									text: emitent.street
								},
								{
									text: emitent.zipCode + ' ' + emitent.city
								},
								{
									text: emitent.country
								}
							],
							width: 180,
							margin: [0, 5, 0, 0]
						},
						{
							margin: [0, -5, 0, 0],
							width: 100,
							stack: [
								{
									layout: minTableLayout(),
									table: {
										headerRows: 0,
										widths: ['auto', 'auto'],
										body: [
											[
												{ text: 'IČO:', alignment: 'right' },
												{ text: emitent.legalId, alignment: 'right' }
											],
											[
												{ text: 'DIČ:', alignment: 'right' },
												{ text: emitent.taxId, alignment: 'right' }
											],
											emitent.taxLegalId && [
												{ text: 'IČ DPH:', alignment: 'right' },
												{ text: emitent.taxLegalId, alignment: 'right' }
											]
										].filter(filterNull) // filter null
									}
								},
								!emitent.taxLegalId && {
									text: 'Nie sme platiteľmi DPH'
								}
							].filter(filterNull)
						}
					],
					fontSize: 8,
					margin: [0, -25, 0, 0]
				},
				{
					text: emitent.email ? 'email: ' + emitent.email : '',
					fontSize: 8,
					margin: [300, emitent.email ? -10 : 0, 0, 0]
				},
				{
					canvas: [
						{ type: 'line', x1: 0, y1: 5, x2: 524, y2: 5, lineWidth: 0.5 }
					]
				},
				{
					columns: [
						{
							text: 'Faktúra',
							bold: true,
							width: '*',
							margin: [0, 5, 0, 0]
						},
						{
							table: {
								body: [
									[
										{
											text: invoice.id,
											margin: [20, 0, 20, 0]
										}
									]
								]
							},
							layout: {
								hLineWidth: function(i, node) {
									return 0.5;
								},
								vLineWidth: function(i, node) {
									return 0.5;
								}
							},
							margin: [0, 0, 0, 0],
							width: '100',
							alignment: 'right'
						}
					],
					margin: [0, 0, 0, 0],
					fontSize: 14
				},
				{
					columns: [
						{ text: '', width: '*' },
						{
							stack: [
								{
									text: 'Odberateľ',
									bold: true
								},
								{
									canvas: [
										{
											type: 'line',
											x1: -2,
											y1: 5,
											x2: 250,
											y2: 5,
											lineWidth: 0.5
										}
									]
								},
								{
									text: recipient.entity,
									bold: true,
									margin: [0, 3, 0, 0]
								},
								{
									text: recipient.street
								},
								{
									text: recipient.zipCode + ' ' + recipient.city
								},
								{
									text: recipient.country
								},
								{
									canvas: [
										{
											type: 'line',
											x1: -2,
											y1: 5,
											x2: 250,
											y2: 5,
											lineWidth: 0.5
										}
									]
								}
							],
							width: 250,
							margin: [0, 20, 0, 0]
						}
					]
				},
				{
					columns: [
						{
							width: 150,
							margin: [0, 20, 0, 0],
							stack: [
								{ text: 'Dátum', bold: true },
								{
									canvas: [
										{
											type: 'line',
											x1: -2,
											y1: 5,
											x2: 122,
											y2: 5,
											lineWidth: 0.5
										}
									]
								}
							]
						},
						{
							width: 150,
							margin: [0, 20, 0, 0],
							stack: [
								{ text: 'Symbol', bold: true },
								{
									canvas: [
										{
											type: 'line',
											x1: -2,
											y1: 5,
											x2: 130,
											y2: 5,
											lineWidth: 0.5
										}
									]
								}
							]
						},
						{
							width: '*',
							margin: [10, 5, 0, 0],
							alignment: 'justify',
							fontSize: 10,
							// HACK: using column hack to make the table float right
							// https://github.com/bpampuch/pdfmake/issues/1318
							columns: [
								{ width: '*', text: '' },
								{
									width: 'auto',
									layout: minTableLayout(),
									table: {
										headerRows: 0,
										widths: ['auto', 'auto'],
										body: [
											[
												{ text: 'IČO:', alignment: 'right' },
												{ text: recipient.legalId, alignment: 'right' }
											],
											[
												{ text: 'DIČ:', alignment: 'right' },
												{ text: recipient.taxId, alignment: 'right' }
											],
											recipient.taxLegalId && [
												{
													text: 'IČ DPH:',
													alignment: 'right'
												},
												{
													text: recipient.taxLegalId,
													alignment: 'right'
												}
											]
										].filter(filterNull) // filter null
									}
								}
							]
						}
					],
					fontSize: 10
				},
				{
					columns: [
						{
							margin: [10, 0, 25, 0],
							width: 150,
							table: {
								headerRows: 0,
								widths: [50, '*'],
								body: [
									[
										{ text: 'vystavenia:' },
										{ text: invoice.issueDate, alignment: 'right' }
									],
									[
										{ text: 'splatnosti:', bold: true },
										{ text: invoice.dueDate, bold: true, alignment: 'right' }
									],
									invoice.deliveryDate && [
										{ text: 'dodania:' },
										{ text: invoice.deliveryDate, alignment: 'right' }
									]
								].filter(filterNull) // filter null
							},
							layout: minTableLayout()
						},
						{
							margin: [10, 0, 25, 0],
							width: 158,
							table: {
								headerRows: 0,
								widths: [60, '*'],
								body: [
									[
										{ text: 'konštantný:' },
										{ text: invoice.constantSymbol, alignment: 'right' }
									],
									[
										{ text: 'variabilný:', bold: true },
										{
											text: invoice.variableSymbol,
											bold: true,
											alignment: 'right'
										}
									]
								]
							},
							layout: minTableLayout()
						},

						{
							margin: [0, 0, 0, 0],
							table: {
								headerRows: 0,
								widths: [80, '*'],
								body: [
									[{ text: 'Platba:', bold: true }, invoice.paymentType || ''],
									[
										{ text: 'Číslo objednávky:', bold: true },
										{ text: invoice.orderNr || '', bold: true }
									]
								]
							},
							layout: minTableLayout()
						}
					],
					fontSize: 9,
					margin: [0, 10, 0, 0]
				},
				{
					columns: [
						{
							stack: [
								{
									text: 'IBAN:',
									bold: true
								},
								{
									table: {
										body: [
											[
												{
													text: emitent.iban,
													bold: true,
													margin: [20, 1, 20, 1]
												}
											]
										]
									},
									layout: {
										hLineWidth: function(i, node) {
											return 0.5;
										},
										vLineWidth: function(i, node) {
											return 0.5;
										}
									},
									margin: [0, 0, 0, 0],
									width: '100',
									alignment: 'right'
								},

								{
									margin: [0, 5, 0, 0],
									table: {
										headerRows: 0,
										widths: [70, '*'],
										body: [
											[
												{ text: 'Bankový účet:', bold: true },
												{ text: emitent.bankAccount, bold: false }
											]
										]
									},
									layout: minTableLayout()
								}
							]
						},
						{
							stack: [
								{
									text: 'SWIFT:',
									bold: true
								},
								{
									table: {
										body: [
											[
												{
													text: emitent.swift,
													bold: true,
													margin: [20, 0, 20, 0],
													fontSize: 11
												}
											]
										]
									},
									layout: {
										hLineWidth: function(i, node) {
											return 0.5;
										},
										vLineWidth: function(i, node) {
											return 0.5;
										}
									},
									margin: [0, 0, 0, 0],
									width: '100',
									alignment: 'right'
								},
								{
									margin: [0, 5, 0, 0],
									table: {
										headerRows: 0,
										widths: [60, '*'],
										body: [
											[
												{ text: 'Kód banky:', bold: true },
												{ text: emitent.bankCode, bold: false }
											]
										]
									},
									layout: minTableLayout()
								}
							]
						}
					],
					margin: [0, 20, 0, 0],
					fontSize: 10
				},
				{
					text: invoice.description || '',
					margin: [0, 10, 0, 0],
					fontSize: 10
				},
				this._getItemTable(invoice),
				{
					text: invoice.finalText,
					margin: [0, 10, 0, 0],
					fontSize: 10
				},
				{
					margin: [200, 20, 0, 0],
					alignment: 'right',
					table: {
						headerRows: 0,
						widths: [150, '*'],
						heights: [10, 20, 10],
						body: this._getPriceTableBody(invoice)
					},
					layout: {
						defaultBorder: false,
						fillColor: function(rowIndex) {
							return rowIndex === 1 ? '#aaa' : null;
						}
					}
				}
			],
			footer: {
				stack: [
					{
						canvas: [
							{ type: 'line', x1: 50, y1: 5, x2: 560, y2: 5, lineWidth: 0.5 }
						]
					},
					{
						columns: [
							{ text: '', width: '25%' },
							{
								text: 'vygenerovala fakturazdarma.sk',
								width: '*',
								alignment: 'center'
							},
							{ text: 'Strana 1/1', width: '25%', alignment: 'right' }
						],
						margin: [50, 5, 50, 50],
						color: '#A9A9A9',
						italics: true
					}
				],
				fontSize: 8
			},
			styles: {}
		};

		return dd;
	}
};

var InvoicePdfDefinitionBuilder = function() {};
InvoicePdfDefinitionBuilder.prototype = DefinitionBuilder;

export default InvoicePdfDefinitionBuilder;
